.root {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    color: $light-2;
    font-size: 18px;

    &__text {
    
        width: 100%;
        text-align: center;
        margin: 10px;
    }
}

.item {
    background-color: $light-2;
    padding: 5px 10px;
    border-radius: 12px;
    margin: 10px;
    color: $dark-1;

    span {
        font-weight: 600;
    }
}

.cool {
    pointer-events: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;

    span {
        display: block;
        -webkit-animation: bounce-top 0.9s both;
        animation: bounce-top 0.9s both;
    }
}