@import "./palette.scss";

.root {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100dvh;
    background-color: $dark-1;

    &__header,
    &__footer {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $dark-2;
        color: $light-2;
        flex-shrink: 0;   
    }

    &__header {
        font-size: 20px;
        border-bottom: 2px dashed $dark-1;
    }

    &__footer {
        border-top: 2px dashed $dark-1;
        padding: 10px;
        justify-content: flex-start;
        margin-top: auto;

        span {
            font-size: 28px;
            line-height: 22px;
            vertical-align: text-bottom;
        }
    }

    &__content {
        width: 100%;
        flex-grow: 1;
        overflow-y: auto;
    }

    &__current {
        flex-grow: 1;
        text-align: center;
    }

    &__button {
        background-color: $dark-3;
        padding: 5px;
        font-size: 32px;
        line-height: 28px;

    }
}