@import "../palette.scss";

.root {
    font-size: 18px;
    width: 100%;
    padding: 10px;
    color: $light-2;

    &__controls {
        display: flex;
        align-items: center;
    }

    &__input {
        font-size: 16px;
        margin-right: 5px;
        background-color: transparent;
        border: 1px solid $dark-3;
        padding: 5px;
        color: $light-2;
        font-size: 18px;
        outline: 0;
        width: 100%;

        &::placeholder {
            color: $dark-3;
        }
    }

    &__count {
        width: 100px;
        display: block;
    }

    &__add {
        font-size: 18px;
        padding: 5px;
    }

    &__item {
        display: flex;
        align-items: center;
        margin: 10px 0;
    }
}

.error {
    color: red;
    font-size: 16px;
}