@import "../palette.scss";

.root {
    padding: 10px;
    font-size: 16px;
    color: $light-2;
    width: 100%;

    &__text {
        width: 100%;
        text-align: center;
        margin-bottom: 10px;
    }
}

.item {
    display: flex;
    align-items: center;
    background-color: $dark-2;
    width: 100%;
    margin-bottom: 10px;
    padding: 5px;
    border-radius: 8px;
    text-align: left;

    &__date {
        font-size: 12px;
        margin-left: auto;
        padding-left: 10px;
        color: $light-1;
        text-align: right;
        white-space: nowrap;
    }
}

.cool {
    pointer-events: none;
    position: fixed;
    z-index: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;

    span {
        position: relative;
        display: block;
        -webkit-animation: slide-out-fwd-center 0.7s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
        animation: slide-out-fwd-center 0.7s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
    }
}

@-webkit-keyframes slide-out-fwd-center {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(10);
        opacity: 0;
    }
}

@keyframes slide-out-fwd-center {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(10);
        opacity: 0;
    }
}